import React, { Component } from 'react';
import { connect } from 'react-redux';
import { string } from 'prop-types';

// Actions
import {
    getMessageValue,
    addTask
} from '../../actions/index';

//Config
import { MAX_LENGTH } from '../../config/index';

class AddTask extends Component {

    onChange = (event) => {
        const { dispatch } = this.props;

        dispatch(getMessageValue(event.target.value));
    }

    onSubmit = (event) => {
        event.preventDefault();

        const { newTaskMessage, dispatch } = this.props;

        if (newTaskMessage.trim() === '') {
            return;
        }

        const task = {
            message:   newTaskMessage,
            completed: false,
            favorite:  false,
            created:   Date.now(),
        };

        dispatch(addTask('', task));
    }

    render () {
        const { newTaskMessage } = this.props;

        return (
            <form
                onSubmit = { this.onSubmit }>
                <input
                    maxLength = { MAX_LENGTH }
                    placeholder = 'ÐÐ¿Ð¸ÑÐ°Ð½Ð¸Ðµ Ð¼Ð¾ÐµÐ¹ Ð½Ð¾Ð²Ð¾Ð¹ Ð·Ð°Ð´Ð°ÑÐ¸'
                    type = 'text'
                    value = { newTaskMessage }
                    onChange = { this.onChange }
                />
                <button>ÐÐ¾Ð±Ð°Ð²Ð¸ÑÑ Ð·Ð°Ð´Ð°ÑÑ</button>
            </form>
        );
    }
}

AddTask.propTypes = {
    newTaskMessage: string.isRequired,
};

const mapStateToProps = (state) => {
    return {
        newTaskMessage: state.newTaskMessage,
    };
};

export default connect(mapStateToProps)(AddTask);
